@import './var';
.mainNav {
  position: fixed;
  // background: ${props =>
  //   props.backgroundFixed ? props.theme.lightOrange : 'transparent'};
  &.fixed {
    background: $lightOrange;
    a {
      color: $offWhite;
    }
  }
  // width: ${props => `${props.width}px`};
  left: 0;
  top: 0;
  z-index: 100;
  transition: background 300ms;
  ul {
    display: flex;
    justify-content: center;
    li {
      padding: 0.5rem 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      &:first-child {
        // margin-right: ${props => (props.mobile ? '0.5rem' : '1rem')};
        margin-right: 1rem;
      }
      a {
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 500;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: color 300ms ease-in-out;
        // color: ${props =>
        //   props.backgroundFixed ? props.theme.offWhite : '#f2f2f2'};
        color: #f2f2f2;
        &.active {
          color: $darkRed;
        }
        div {
          display: flex;
          align-items: flex-end;
          & > *:nth-child(2) {
            // margin-left: ${props => (props.mobile ? '5px' : '10px')};
            margin-left: 10px;
          }
          img {
            width: auto;
            object-fit: cover;
          }
          img:nth-child(1) {
            // height: ${props => (props.mobile ? '50px' : '60px')};
            height: 60px;
          }
          img:nth-child(2) {
            // height: ${props => (props.mobile ? '30px' : '38px')};
            height: 38px;
          }
        }
      }
    }
  }
}
